html,
body {
  height: 100%;
}
body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
  background-color: #f6f6f6;
  overflow: auto;
}

footer {
  margin-top: auto;
}

.Layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
.Layout-main {
  margin-left: 25px;
  margin-bottom: 25px;
}

.Header-logo-text {
  margin-left: 12px;
}

.Footer {
  background-color: #20232a;
  color: #ffffff;
  padding-top: 20px;
  padding-bottom: 30px;
}
.Footer-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.Footer-github {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}
.Footer-github:hover {
  color: white;
}
.Footer-github-text {
  margin-left: 10px;
}

.Blog-posts-by-year {
  margin-top: 12px;
}
.Blog-list {
  list-style: none;
}
.Blog-post {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.Blog-list li {
  line-height: 1.75em;
}
.Blog-list .Blog-date {
  color: rgba(0, 0, 0, 0.5);
  margin-right: 7px;
}

.PostLayout {
  padding: 10px;
  margin-bottom: 30px;
}
.PostLayout p {
  font-size: 16px;
}

.PostImage {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}


.NotFound {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
.NotFound-header {
  font-weight: 700;
  font-size: 6rem;
  color: #20232a;
}
.NotFound-sub-header {
  font-size: 2rem;
  color: #aaa;
}

